  .skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px 100px;
  }
  
  .skills h1 {
    font-size: 56px;
    font-weight: 700;
    color: #fbfbfb;
    font-family: "Libre Baskerville", serif;
  }
  
  .cards {
    display: flex;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    gap: 2.5vh;
  }
  
  .skill-card {
    height: 20vw;
    width: 20vw;
    background-color: #3d3e42;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    gap: 2.5vh;
    transition: all ease 0.5s;
  }
  .skills .cards a{
    text-decoration:none;
  }
  
  .skill-card:hover {
    background-color: #fec86a;
  }
  
  .skill-card img {
    height: 7.5vw;
    width: 7.5vw;
    object-fit: contain;
    
  }
  
  .skill-card h2 {
    font-size: 24px;
    font-weight: 400;
    color: #fbfbfb;
  }



  .experience {
    display: flex;
    flex-direction: column;
    /* height: 100vh; */
    width: 97.5vw;
    position: relative;
    align-items: center;
    justify-content: center;
  }
  
  .darker-div {
    background-color: #34353a;
    width: 100vw;
    height: 10vh;
  }
  
  .experience h1 {
    position: absolute;
    color: #fbfbfb;
    font-size: 3.5vw;
    font-family: "Libre Baskerville", serif;
    transform: translate(-50%, -50%);
    top: 10%;
    left: 15%;
  }
  
  .lighter-div {
    background-color: #3d3e42;
    width: 100vw;
    /* height: 90vh; */
    padding: 10vh;
    display: flex;
    flex-direction: column;
    gap: 50px;
  }
  
  .lighter-div .exp {
    height: 40vh;
    width: 50vw;
    position: relative;
    margin-left: 20vh;
  }
  

  .exp h2 {
    color: #fbfbfb;
    font-size: 2.5vw;
    font-weight: 400;
  }

  .exp-desc span{
    font-weight: 500;
    margin-right: 3px;
    /* font-size: 2.8rem; */
    color: #fec86a;
  }
  
  .exp h2::before {
    content: "";
    height: 20px;
    width: 20px;
    color: #fec86a;
    background-color: #fec86a;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 7.5%;
    left: -5%;
    border-radius: 50%;
    box-shadow: 0px 0px 20px 5px #fec86a;
  }
  
  .exp-desc::before {
    content: "";
    height: 220px;
    width: 2.5px;
    background-color: #fbfbfb;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 36%;
    left: -5%;
  }
  .first-exp .exp-desc::before {
    content: "";
    height: 280px;
    width: 2.5px;
    background-color: #fbfbfb;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 49%;
    left: -5%;
  }
  .exp-desc h3 {
    color: #fbfbfb;
    font-size: 1.5vw;
    font-weight: 400;
    margin: 10px 0;
  }
  
  .exp-desc p {
    color: #fbfbfb;
    font-size: 1vw;
    font-weight: 400;
    margin-top: 0;
    /* position: absolute; */
  }
  
